
.popup-video{
  position: fixed;
  z-index: 555;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.8);
  padding: 5px;
  display: none;
  overflow: auto;
  .holder{
    max-width: 768px;
    padding-top: 30px;
    @include media('>=xtablet'){
      padding-top: 0;
    }
    @include media('>=large'){
      max-width: 1100px;
    }
    width: 100%;
    max-height: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .box{
    position: relative;
    padding-top: 56.25%;
    height: 0;
  }
  .btn.close{
    width: 30px;
    height: 30px;
    position: absolute;
    padding: 0;
    z-index: 5;
    right: 0;
    top: 0;
    background-color: transparent;
    cursor: pointer;
    @include media('>=xtablet'){
      right: -50px;
    }
    @include media('>=desktop'){
      width: 52px;
      height: 52px;
      right: -83px;
    }
    &:after, &:before{
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      width: 2px;
      height: 20px;
      transform: translate(-50%, -50%) rotate(45deg);
      background-color: $color-white;
    }
    &:after{
      transform: translate(-50%, -50%) rotate(-45deg);
    }
  }
  iframe{
    width: 100% !important;
    height: 100% !important;
    max-width: 100%;
    max-height: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}